@media only screen and (max-width: 992px){
	section{
		padding: 40px 0;
	}
	.header{
		&__burger{
			display: block;
			position: relative;
		}
	}
	.menu{
		position: absolute;
		right: 30px;
		top: 5px;
		display: none;
		&__list{
			display: block;
			padding: 10px 10px;
			border: 1px solid $color-btn;
		}
		&__link{
			display: block;
			margin: 10px 0;
		}
		&__active{
			display: inline-block;
			animation-name: burgerMenuActive;
			animation-duration: 0.6s;
		}
	}
	@keyframes burgerMenuActive{
		from {opacity: 0;}
		to {opacity: 1;}
	}
	
	.dots{
		padding: 50px 0 40px 0;
	}
	.works{
		&__content{
			h5{
				padding-top: 20px;
			}
		}
	}
	.contact{
		&__title{
			&:last-child{
				display: none;
			}
		}
		&__clients{
			display: none;
		}
	}
	.footer{
		&__logo{
			margin-bottom: 10px;
		}
		&__address{
			margin-top: 26px;
		}
		&__form{
			margin-top: 10px;
		}
	}
}

@media only screen and (max-width: 768px){
	.header{
		&__burger{
			right: 40px;
		}
	}
	.menu{
		right: 50px;
	}
	.works{
		&__item{
			padding: 0 0 30% 0;
		}
	}
	.map{
		&__descr{
			p{
				margin-bottom: 30px;
			}
		}
		&__yandex{
			
		}
	}
	.footer{
		&__address{
			margin-top: 15px;
		}
	}
}

@media only screen and (max-width: 576px){
	.header{
		&__burger{
			right: 60px;
		}
	}
	.menu{
		right: 80px;
	}
	.header-descr{
		&__title{
			font-size: 24px;
		}
		&__subtitle{
			font-size: 12px;
		}
	}
	.prev{
		margin-left: -40px;
	}
	.next{
		right: 0;
		margin-right: -40px;
	}
	.about{
		&__logo{
			display: none;
		}
	}
	.works{
		&__item{
			padding: 0 0 30% 0;
		}
	}
	.footer{
		padding: 30px 0;
	}
}